import * as React from 'react';

import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

import 'leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';

import {Grid, Paper, Divider, Typography, Slider, Box} from '@mui/material';
import { styled } from '@mui/material/styles';

import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import {FormControl, FormGroup, FormControlLabel} from '@mui/material';
import ListItemText from '@mui/material/ListItemText';
import Select from '@mui/material/Select';
import Checkbox from '@mui/material/Checkbox';

import Switch from '@mui/material/Switch';

import data from '../assets/data.json';

import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';

let DefaultIcon = L.icon({
    iconUrl: icon,
    shadowUrl: iconShadow
});

L.Marker.prototype.options.icon = DefaultIcon;

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: '#fff',
  ...theme.typography.body1,
  padding: theme.spacing(1),
  textAlign: 'left',
  color: theme.palette.text.primary,
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const secteurs = [
    'Entrepot',
    'Bureau',
    'Parking'
];


export default class MapComponent extends React.Component {
    constructor() {
        super();
        this.state = {
            slider1 : 2016,
            slider2 : 20,
            boolValue : true,
            strValue : "hello",
            secteursList : []
        };
    }


    componentDidMount() {
        var map = L.map('map', {
            center: [48.8481232,2.351374], // EDIT coordinates to re-center map
            zoom: 6,  // EDIT from 0 (zoomed out) to 18 (zoomed in)
            scrollWheelZoom: true,
            tap: false
        });
        /*var tiles = L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXVycTA2emYycXBndHRqcmZ3N3gifQ.rJcFIG214AriISLbB6B5aw', {
            maxZoom: 18,
            attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1
        }).addTo(map);*/ // MapBox Tiles
        L.tileLayer('https://{s}.tile.openstreetmap.fr/osmfr/{z}/{x}/{y}.png', {
            maxZoom: 18,
            attribution: '&copy; <a href="https://openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        }).addTo(map);

        var markersCluster = new L.MarkerClusterGroup();

        for (let key in data) {
            var latLng = new L.LatLng(data[key]['lat'], data[key]['lon']);
            var marker = new L.Marker(latLng, {title: data[key]['Num_DAU']});
            marker.bindPopup(`DAU ${data[key]["Num_DAU"]}`);
            markersCluster.addLayer(marker);
        }

        map.addLayer(markersCluster);
    }

    render() {
        const handleChange = (event) => {
            const {
              target: { value },
            } = event;
            this.setState({
                // On autofill we get a stringified value.
                secteursList: typeof value === 'string' ? value.split(',') : value,
            });
        };

        return (
            <Grid container spacing={2} sx={{height : 'calc(100vh - 64px - 2*24px + 16px)'}}>
                <Grid item xs={8}>
                    <Item sx={{height: '100%'}} id='map'>
                    </Item>
                </Grid>
                <Grid item xs={4} sx={{ height : '100%'}}>
                    <Item sx={{ height : '100%', overflow : 'auto' }}>Filtres
                        <Divider/>
                        <br/>
                        <Typography id="track-false-slider" gutterBottom>Année</Typography>
                        <Slider aria-label="Temperature" defaultValue={2016}
                                getAriaValueText={(value) => '$value'}
                                valueLabelDisplay="on"
                                marks
                                min={2013} max={2021} step={1}/>

                        <Typography id="track-false-slider" gutterBottom>Surface minimale</Typography>
                        <Slider aria-label="slider2" defaultValue={5000}
                                getAriaValueText={(value) => `${value}`}
                                valueLabelDisplay="on"
                                min={0} max={10000} color="secondary"
                        />
                        <FormGroup>
                          <FormControlLabel control={<Switch defaultChecked color="warning"/>} label="Nouveaux sites uniquement" />
                          <FormControlLabel control={<Switch />} label="Sites déjà visités" />
                        </FormGroup>
                        <FormControl sx={{ m: 1, width: 300 }}>
                        <InputLabel id="demo-multiple-checkbox-label">Secteur</InputLabel>
                        <Select
                          labelId="demo-multiple-checkbox-label"
                          id="demo-multiple-checkbox"
                          multiple
                          value={this.state.secteursList}
                          onChange={handleChange}
                          input={<OutlinedInput label="Secteur" />}
                          renderValue={(selected) => selected.join(', ')}
                          MenuProps={MenuProps}
                        >
                          {secteurs.map((sec) => (
                            <MenuItem key={sec} value={sec}>
                              <Checkbox checked={this.state.secteursList.indexOf(sec) > -1} />
                              <ListItemText primary={sec} />
                            </MenuItem>
                          ))}
                        </Select>
                        </FormControl>

                        <Divider/>
                    </Item>
                </Grid>

            </Grid>
        );
    }
}

//export default MapComponent;